.logo_des {
    display: grid;
    grid-template-areas: "logo des";
    grid-template-columns: 180px 800px;
    
}

.logo {
    grid-area: logo;
    margin: 55px 75px;
    padding: 0;
}

.descriptionH{
    font-size: 28px;
    font-weight: 500;
    margin-bottom: 20px;
}

.description {
    grid-area: des;
    padding: 0;
    margin: 55px 75px;
    font-size: 18px;
}