.item {
    color: rgb(88, 88, 88);
    padding: 0;
    margin: 10px 60px;
    display: grid;
    grid-template-areas: 'i m';
    grid-template-columns: 2fr 10fr;
}

.item img {
    width: 50px;
    height: 50px;
    border-radius: 50px;
    grid-area: 'i';
}

.postText {
    grid-area: 'm';
    margin: 15px 0 0 0;
}